// Variables
@import "variables";

.navbar{
	margin-bottom:0px;
}

.no-margin{
	margin:0px;
}

.top-space{
	margin-top:1em;
}

.no-padding{
	padding:0px;
}

.large-padding{
	padding:10px 15px;
}

.big-padding{
	padding:30px;
}

.no-transform{
	text-transform:none;
}

.blue-grey{
	background-color:#37474f;
}

.white-text{
	color:white;
}

.red-text{
	color:rgb(200, 50, 50);
}

.inline-block{
	display:inline-block;
}

.white{
	background-color:white;
}

.table{
	background-color:white;
}

.absolute{
	position:absolute;	
}

.floating{
	position:fixed;
	bottom:2em;
	right:2em;
	z-index:100;
}

.product{
	width:600px;
	max-width:100%;
	padding:1em;
	position:relative;

	h1{
		padding-bottom:10px;
		border-bottom:solid 1px;
	}

	.actions{
	right: 1em;
	top:10px;
	}
}

.creado{
	background-color:#d4e157;
}

.sale-data{
	padding:1em;
	text-align:center;
	border-right:solid 1px #222;
	span{
	display:block;
	font-size:2.2em;
	color:#3f51b5;
	}

}

.product-avatar{
	max-width: 70%;
}

.products-container{
	.product{
	margin-top:1em;
	}
}

.circle-shopping-cart{
	background-color:#232980;
	border-radius:50%;
	padding: 0.5em 0.5em;

}

.highlight{
	-webkit-animation: highlight 1.5s;
	animation: highlight 1.5s;
}

@-webkit-keyframes highlight{
	50% {
		background-color:#ffff00;
		color:black;
	}
	100% {
		background-color: inherit;
	}
}

@keyframes highlight{
	50% {
		background-color:#ffff00;
		color:black;
	}
	100% {
		background-color: inherit;
	}
}

.style-text{
	    font-weight: bold;
}